.PersonalCard {
    height: 380px;
    width: 320px;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    text-align: center;
    flex-wrap: wrap;

    border: solid 2px #aaa;
    border-radius: 24px;
    margin: 10px;

    .AvatarContainer {
        height: 200px;
        width: 320px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }

    a {
        text-decoration: none;
        color: black;
    }
}

.Avatar {
    cursor: pointer;
    display: block;
    margin: auto;
    transition: 0.7s;
    width: 160px;

    border-radius: 50%;

    border: solid 2px #555;
}

.Avatar:hover {
    width: 168px;
}