#prezentare {
    width: 100%;
}

#PrezentareScreen {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;

    #VennDiagramContainer {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: middle;
        #VennDiagram {
            width: 500px;
            max-width: 100%;
        }
    }
    @media only screen and (max-device-width : 999px) {
        #PrezentareText {
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            align-items: center;
            width: 100%;

            .TextContainer {
                width: 100%;
                height: min-content;
                text-align: center;
            }
        }
    }
    @media only screen and (min-device-width : 1000px) {
        #PrezentareText {
            width: 500px;
            min-height: 500px;

            .TextContainer {
                transform: translateY(100px);
                width: 100%;
                height: min-content;
            }
        }
    }
}