#Menu {
    max-width: 1200px;
    width: calc(#{100%});
    height: 100px;

    position: fixed;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 100;

    background-color: white;
    border-bottom: solid 2px #555;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;

    box-sizing: border-box;
    padding-left: 20px;
    padding-right: 20px;

    #Menus {
        #MenuButton {
            width: 50px;
        }
    }
}

#NavMenu {
    transition: 0.75s;
    position: fixed;
    top: 100px;
    left: max(0px, calc((#{100vw} - #{1200px}) / 2));

    background-color: #fff;
    filter: drop-shadow(5px 15px 8px #555);

    max-width: 100vw;
    width: 240px;
    height: calc(#{100vh} - #{100px});
    z-index: 100;

    text-align: center;

    .MenuLink {
        font-size: larger;
        text-decoration: underline;
        color: black;
    }
}


.NavMenuClosed {
    transform: translateX(-270px);
}

.NavMenuOpened  {
    transform: translateX(0);
}